<template>
    <div class="pos">
      <el-row>
        <el-col :span="7" class="pos-order" id="order-list">
          <el-tabs>
            <el-tab-pane label="点餐">
              <el-table :data="tableData" border style="width: 100%">
                <el-table-column prop="goodsName" label="商品名称"></el-table-column>
                <el-table-column prop="count" label="数量" width="70"></el-table-column>
                <el-table-column prop="price" label="金额" width="70"></el-table-column>
                <el-table-column  label="操作" width="100" fixed="right">
                  <template scope="scope">
                    <el-button type="text" size="small" @click="delSingGoods(scope.row)">删除</el-button>
                    <el-button type="text" size="small" @click="addOrderList(scope.row)">增加</el-button>
                  </template>
                </el-table-column>
              </el-table>
              <div>
                数量：{{totalCount}}&nbsp;&nbsp;&nbsp;金额：{{totalMoney}}元
              </div>
              <div class="divbtn">
                <el-button type="warning" size="small">挂单</el-button>
                <el-button type="danger" size="small" @click = "delAll()">删除</el-button>
                <el-button type="info" size="small" @click="checkout()">结账</el-button>
              </div>

            </el-tab-pane>
            <el-tab-pane label="挂单">
              挂单
            </el-tab-pane>
            <el-tab-pane label="外卖">
              外卖
            </el-tab-pane>
          </el-tabs>
        </el-col>
        <el-col :span="17">
          <div class="often-goods">
            <div class="title">常用商品</div>
            <div class="often-goods-list">
              <ul>
                <li v-for="goods in tenGoods" @click="addOrderList(goods)">
                  <span>{{goods.goodsName}}</span>
                  <span class="o-price">￥{{goods.price}}元</span>
                </li>
              </ul>
            </div>
          </div>

          <div class="goods-type">
            <el-tabs>
              <el-tab-pane label="汉堡">
                <div>
                  <ul class="cookList">
                    <li v-for="cookList in type0Goods" @click="addOrderList(cookList)">
                      <span class="foodImg"><img :src="cookList.goodsImg" width="100%"></span>
                      <span class="foodName">{{cookList.goodsName}}</span>
                      <span class="foodPrice">￥{{cookList.price}}元</span>
                    </li>
                  </ul>
                </div>
              </el-tab-pane>
              <el-tab-pane label="小食">
                <div>
                  <ul class="cookList">
                    <li v-for="cookList in type1Goods" @click="addOrderList(cookList)">
                      <span class="foodImg"><img :src="cookList.goodsImg" width="100%"></span>
                      <span class="foodName">{{cookList.goodsName}}</span>
                      <span class="foodPrice">￥{{cookList.price}}元</span>
                    </li>
                  </ul>
                </div>
              </el-tab-pane>
              <el-tab-pane label="饮料">
                <div>
                  <ul class="cookList">
                    <li v-for="cookList in type2Goods" @click="addOrderList(cookList)">
                      <span class="foodImg"><img :src="cookList.goodsImg" width="100%"></span>
                      <span class="foodName">{{cookList.goodsName}}</span>
                      <span class="foodPrice">￥{{cookList.price}}元</span>
                    </li>
                  </ul>
                </div>
              </el-tab-pane>
              <el-tab-pane label="套餐">
                <div>
                  <ul class="cookList">
                    <li v-for="cookList in type3Goods" @click="addOrderList(cookList)">
                      <span class="foodImg"><img :src="cookList.goodsImg" width="100%"></span>
                      <span class="foodName">{{cookList.goodsName}}</span>
                      <span class="foodPrice">￥{{cookList.price}}元</span>
                    </li>
                  </ul>
                </div>
              </el-tab-pane>
            </el-tabs>
          </div>
        </el-col>
      </el-row>

    </div>
</template>

<script type="text/ecmascript-6">
  import  axios from 'axios'
/*    import ElRow from "element-ui/packages/row/src/row";
    import ElCol from "element-ui/packages/col/src/col";
    import ElTabPane from "../../../node_modules/element-ui/packages/tabs/src/tab-pane.vue";
    import ElTable from "../../../node_modules/element-ui/packages/table/src/table.vue";*/

//    import ElButton from "../../../node_modules/element-ui/packages/button/src/button.vue";
//import ElTabs from "../../../node_modules/element-ui/packages/tabs/src/tabs.vue";
//import ElTabPane from "../../../node_modules/element-ui/packages/tabs/src/tab-pane.vue";

export default {
      /*components: {
        ElTable,
        ElTabPane,
        ElCol,
        ElRow},*/
  /*components: {
    ElTabPane,
    ElTabs,
    ElButton},*/
  name: "pos",
  data(){
    return{
      tableData: [],
      tenGoods:[],
      type0Goods:[],
      type1Goods:[],
      type2Goods:[],
      type3Goods:[],
      totalMoney:0,
      totalCount:0
    }
  },
  created:function () {
    axios.get('http://jspang.com/DemoApi/oftenGoods.php')
      .then(response=>{
//        console.log(response)
        this.tenGoods = response.data;
      })
      .catch(error=>{
        console.log(error);
        alert('网络错误！！')
      })

    axios.get('http://jspang.com/DemoApi/typeGoods.php')
      .then(response=>{
//        console.log(response)
        this.type0Goods = response.data[0];
        this.type1Goods = response.data[1];
        this.type2Goods = response.data[2];
        this.type3Goods = response.data[3];
      })
      .catch(error=>{
        console.log(error);
        alert('网络错误！！')
      })

  },
  mounted:function () {
    var orderHeight = document.body.clientHeight;
    document.getElementById("order-list").style.height=orderHeight +'px'
  },
  methods:{
    addOrderList(goods){
      this.totalMoney = 0;
      this.totalCount = 0;
     //商品是否已存在商品订单列表中
      let isNave = false;
      for(let i=0; i<this.tableData.length; i++){
        if(this.tableData[i].goodsId == goods.goodsId){
          isNave=true;
        }
      }
     // 根据判断编写业务逻辑
      if(isNave){
        /*改变商品数量*/
        let arr = this.tableData.filter(o=>o.goodsId == goods.goodsId);
        arr[0].count++;
      }else {
        let newGoods={goodsId:goods.goodsId,goodsName:goods.goodsName,price:goods.price,count:1};
        this.tableData.push(newGoods);
      }
      this.getAllMoney()
    },
    /*删除单个商品 */
    delSingGoods(goods){
      this.tableData = this.tableData.filter(o=>o.goodsId != goods.goodsId);
      this.getAllMoney();

    },
    delAll(){
      this.tableData = [];
      this.totalCount = 0;
      this.totalMoney = 0;
    },
    checkout(){
      if(this.totalCount != 0){
        this.tableData = [];
        this.totalCount = 0;
        this.totalMoney = 0;
        this.$message({
          message:'结账成功！！',
          type:'success'
        })
      }else {
        this.$message.error('不能空结')
      }
    },
    getAllMoney(){
      this.totalMoney = 0;
      this.totalCount = 0;
      if(this.tableData){
        /*计算汇总金额数量*/
        this.tableData.forEach((element)=>{
          this.totalCount += element.count;
          this.totalMoney = this.totalMoney+(element.price*element.count);
        })
      }
    }
  }
}
</script>
<style scoped>
  .pos-order{
    background-color: #fff;
    border-left:1px solid lightblue;
  }
  .divbtn{
    margin-top:15px;
  }
  .title{
    height: 20px;
    border-bottom: 1px solid darkturquoise;
    background-color: lightblue;
    padding: 10px;
    text-align: left;
  }
  .often-goods-list ul li{
    list-style: none;
    float: left;
    border: 1px solid khaki;
    padding: 10px;
    margin: 10px;
    /*border-bottom: 10px;*/
    background-color: #fff;
    cursor: pointer;
  }
  .o-price{
    color: darkturquoise;
  }
  .goods-type{
    clear: both;
  }
  .cookList li{
    list-style: none;
    width:23%;
    border:1px solid #E5E9F2;
    height: auot;
    overflow: hidden;
    background-color:#fff;
    padding: 2px;
    float:left;
    margin: 2px;
    cursor: pointer;

  }
  .cookList li span{

    display: block;
    float:left;
  }
  .foodImg{
    width: 40%;
  }
  .foodName{
    font-size: 18px;
    padding-left: 10px;
    color:brown;

  }
  .foodPrice{
    font-size: 16px;
    padding-left: 10px;
    padding-top:10px;
  }
</style>



